<template>
  <div>
    <el-button type="primary" @click="recirdingFormVisible = true">设置Har文件录制</el-button>
    <el-dialog title="设置Har文件录制" :visible.sync="recirdingFormVisible">
      <el-form :model="recirdingForm">
        <el-form-item label="正则表达式ID" :label-width="formLabelWidth">
          <el-input v-model="recirdingForm.match" auto-complete="off" type="text"></el-input>
        </el-form-item>
        <el-form-item label="文件名称" :label-width="formLabelWidth">
          <el-input v-model="recirdingForm.fileName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="recirdingFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="addRecirding()">确 定</el-button>
      </div>
    </el-dialog>
    <el-button type="danger" @click="clearRecirdingFormVisible = true">清理Har文件录制</el-button>
    <el-dialog title="设置Har文件录制" :visible.sync="clearRecirdingFormVisible">
      <p>点击确定清空配置</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearRecirdingFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="clearRecirding()">确 定</el-button>
      </div>
    </el-dialog>
    <el-button type="danger" @click="clearRecirdingFilesFormVisible = true">清理Har文件录制</el-button>
    <el-dialog title="设置Har文件录制" :visible.sync="clearRecirdingFilesFormVisible">
      <p>点击确定清空配置</p>
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearRecirdingFilesFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="clearRecirdingFiles()">确 定</el-button>
      </div>
    </el-dialog>
    <el-collapse accordion>
      <el-collapse-item title="已经保存的Har文件">
        <div>
          <el-table
            :data="harFileListData"
            style="width: 100%">
            <el-table-column
              prop="file_name"
              label="文件名称">
            </el-table-column>
          </el-table>
        </div>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import {HarFileList, ClearHarConfig, HarConfig, ClearHarFiles} from '../config/api'
import {AjaxHelper} from '@/config/AjaxHelper'
//var ajaxHelper = new AjaxHelper()
export default {
  data() {
    return {
      tableData: [],
      harFileListData: [],
//      录制内容
      recirdingFormVisible: false,
      recirdingForm: {
        match: "",
        fileName: "",
      },
//      清理录制配置
      clearRecirdingFormVisible: false,
//      清空har文件
      clearRecirdingFilesFormVisible: false,
//      删除部分
      deleteFormVisible: false,
      deleteform: {
        id: '',
      },
      clearFormVisible: false,
      cancelFormVisible: false,
      formLabelWidth: '120px'
    };
  },
  created() {
    this.FileList()
  },
  mounted() {
    window.setInterval(() => {
      this.FileList()
    }, 2000)
  },
  complete() {
  },
  methods: {
    FileList() {
      this.$http.get(HarFileList)
        .then(res => {
          console.log(res)
          return res.json()
        })
        .then(data => {
            console.log(data.data)
            this.harFileListData = data.data
          }
        )
    },
    clearRecirding() {
      this.$http.get(ClearHarConfig)
        .then(res => {
          return res.json()
        })
        .then(data => {
          if (data.code == 'error') {
            this.$message({
              showClose: true,
              message: '接口有错误，清理失败！'
            })
            ;
          }
          else {
            this.clearRecirdingFormVisible = false;
          }
        })
    },
    addRecirding() {
      this.$http.post(HarConfig, this.recirdingForm)
        .then(res => {
          return res.json()
        })
        .then(data => {
          if (data.code == 'error') {
            this.$message({
              showClose: true,
              message: '接口有错误，添加失败！'
            })
            ;
          }
          else {
            this.recirdingFormVisible = false;
          }
        })
    },
    clearRecirdingFiles() {
      this.$http.get(ClearHarFiles)
        .then(res => {
          return res.json()
        })
        .then(data => {
          if (data.code == 'error') {
            this.$message({
              showClose: true,
              message: '接口有错误，清理失败！'
            })
            ;
          }
          else {
            this.clearRecirdingFilesFormVisible = false;
          }
        })
    },
  }
}
</script>



